/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.separacion {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .separacion {
    padding: 8rem 0;
  }
}

.sitio-header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 5rem 0 0 0;
}
.sitio-header .logo {
  width: 20rem;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .sitio-header .logo {
    margin: 0;
  }
}
.sitio-header .contenido-header {
  padding: 15rem 0;
}
.sitio-header .header-titulo {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .sitio-header .header-titulo {
    max-width: 65%;
    font-size: 6rem;
  }
}
@media (min-width: 1024px) {
  .sitio-header .header-titulo {
    max-width: 40%;
  }
}

@media (min-width: 768px) {
  .podcast .podcast-grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.podcast .contenido-podcast {
  margin: 5rem 0 0 0;
}
@media (min-width: 768px) {
  .podcast .contenido-podcast {
    margin: 0;
  }
}
.podcast .titulo-podcast {
  font-size: 3.6rem;
  display: inline-block;
  color: #313c4e;
  margin: 5rem 0 0 0;
}
@media (min-width: 768px) {
  .podcast .titulo-podcast {
    margin: 0;
  }
}
.podcast .titulo-podcast::after {
  content: "";
  background-color: #84D9Bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.5rem 0 0 0;
}

.dinero {
  background-color: #84D9Bc;
}
@media (min-width: 768px) {
  .dinero .dinero-grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.dinero .contenido-dinero {
  grid-column: 1/4;
}
.dinero .imagen-dinero {
  grid-column: 4/6;
  transition: transform 0.3s ease;
}
.dinero .imagen-dinero:hover {
  transform: rotate(10deg) scale(1.1);
}
.dinero .dinero-titulo {
  font-size: 3.6rem;
  display: inline-block;
  color: #313c4e;
  margin: 5rem 0 0 0;
}
@media (min-width: 768px) {
  .dinero .dinero-titulo {
    margin: 0;
  }
}
.dinero .dinero-titulo::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.5rem 0 0 0;
}

.estadisticas .titulo-estadisticas {
  font-size: 3.6rem;
  display: inline-block;
  color: #313c4e;
  margin: 5rem 0 0 0;
}
@media (min-width: 768px) {
  .estadisticas .titulo-estadisticas {
    margin: 0;
  }
}
.estadisticas .titulo-estadisticas::after {
  content: "";
  background-color: #84D9Bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.5rem 0 0 0;
}

.planes {
  background-color: #313c4e;
}
.planes .titulo-planes {
  font-size: 3.6rem;
  display: inline-block;
  color: #FFFFFF;
  margin: 5rem 0 0 0;
}
@media (min-width: 768px) {
  .planes .titulo-planes {
    margin: 0;
  }
}
.planes .titulo-planes::after {
  content: "";
  background-color: #84D9Bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.5rem 0 0 0;
}
.planes .texto-planes {
  color: #FFFFFF;
}
.planes .plan {
  padding: 15rem 4rem 4rem;
  text-align: center;
  flex-grow: 1;
  border-bottom: 1px solid #e1e1e1;
}
@media (min-width: 768px) {
  .planes .plan {
    border: none;
    transition-property: transform;
    transition-duration: 300ms;
  }
  .planes .plan:hover {
    transform: scale(1.1);
  }
}
.planes .planes-grid {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .planes .planes-grid {
    flex-direction: row;
    max-width: 80rem;
    margin-inline: auto;
    margin-block-start: 10rem;
  }
}
.planes .inicio,
.planes .pro {
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .planes .inicio,
.planes .pro {
    padding: 15rem 2rem 2rem 2rem;
  }
}
.planes .inicio {
  background-image: url("../img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.planes .negocio {
  order: -1;
  background-color: #84D9Bc;
  background-image: url("../img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
@media (min-width: 768px) {
  .planes .negocio {
    order: 0;
    margin: -5rem 0;
    border-radius: 2rem;
    z-index: 100;
    padding: 20rem 2rem 2rem 2rem;
    background-position: 50% 7rem;
  }
}
.planes .pro {
  background-image: url("../img/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 2rem;
  background-size: 10rem;
}
.planes .listado {
  margin: 0;
}
.planes .listado li {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .planes .listado li:last-of-type {
    margin-bottom: 0;
  }
}
.planes .precio {
  font-weight: 900;
  font-size: 2rem;
  text-transform: uppercase;
}

@media (min-width: 1024px) {
  .comentarios .comentarios-grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.comentarios .comentarios-imagen {
  grid-column: 1/3;
  width: 70%;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .comentarios .comentarios-imagen {
    width: 100%;
  }
}
.comentarios .comentarios-contenido {
  grid-column: 3/6;
}
.comentarios .comentarios-titulo {
  font-size: 3.6rem;
  display: inline-block;
  color: #313c4e;
  margin: 5rem 0 0 0;
}
@media (min-width: 768px) {
  .comentarios .comentarios-titulo {
    margin: 0;
  }
}
.comentarios .comentarios-titulo::after {
  content: "";
  background-color: #84D9Bc;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -1.5rem 0 0 0;
}
.comentarios .comentarios-texto {
  background-color: #84D9Bc;
  color: #313c4e;
  line-height: 2.5;
  padding: 2rem 1rem 2rem 7rem;
  border: 0.5rem solid #313c4e;
  border-radius: 1rem;
  position: relative;
  margin-top: 4rem;
}
@media (min-width: 1024px) {
  .comentarios .comentarios-texto {
    max-width: 70%;
  }
}
.comentarios .comentarios-texto::before {
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  background-position: center;
  display: block;
  height: 5rem;
  width: 5rem;
  position: absolute;
  left: 1rem;
}
.comentarios .comentarios-texto p {
  margin: 0;
  font-size: 1.8rem;
}
@media (min-width: 1024px) {
  .comentarios .comentarios-texto:nth-child(2) {
    margin-left: 10%;
  }
  .comentarios .comentarios-texto:nth-child(3) {
    margin-left: 20%;
  }
  .comentarios .comentarios-texto:nth-child(4) {
    margin-left: 30%;
  }
}

.footer {
  background-color: #313c4e;
}
.footer .logo-footer {
  width: 20rem;
  margin: 0 auto 5rem auto;
}
.footer .contenido-footer {
  padding: 5rem 0;
  border-bottom: 2px solid #84D9Bc;
}
@media (min-width: 1024px) {
  .footer .contenido-footer {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer .info {
  text-align: left;
}
.footer .info h3 {
  color: #84D9Bc;
  text-align: left;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.2rem;
  text-transform: uppercase;
  margin: 2rem 0 0 0;
}

.boton-primario, .boton-planes {
  background-color: #84D9Bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.boton-primario:hover, .boton-planes:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .boton-primario, .boton-planes {
    width: auto;
    display: inline-block;
  }
}

.boton-planes {
  margin-top: 2rem;
  border: 2px solid #FFFFFF;
}

.boton-footer {
  display: block;
  margin-bottom: 1rem;
  color: #FFFFFF;
}